@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400&display=swap);
body {
  background-color: whitesmoke !important;
  font-family: 'Nunito', sans-serif;
}

.my-container{
  position: relative;
  top: 7vh;
}
.open-upload-form {
  position: relative;
  top: 90px;
  margin: 0 auto;
  width: 140px;
  height: 50px;
  background-color: #36d68b;
  display: flex;
  justify-content:center;
  align-items:center;
  color: white !important;
  font-weight: 500;
  font-size: 19px;
  cursor: pointer;
  border-radius: 7px;
  text-decoration: none !important;
}

.swal2-confirm , 
.swal2-confirm :hover,
.swal2-confirm :active {
    font-weight: 500;
    color: #ffffff !important;
    margin: 10px;
    background-color: #36d68b !important;
    border: 1px solid #ffffff !important;
}
.swal2-confirm:focus{
    box-shadow: 0 0 0 0 !important;
}


.swal2-header > h2 {
  margin: 0;
  font-size: 20px;
}

#swal2-content {
  font-size: 17px;
  margin-top: 10px;
}

@media (max-width: 650px) {
  .my-container {
    top: 0vh !important;
    bottom: 7vh !important;
  }
}
.carousel-inner {
  display: flex;
  width: 80%;
  margin: 0 auto;
}
.about h1 {
  text-align: center;
}
.about > div {
  font-size: 19px;
}
.about {
  margin-top: 25px;
}
.carousel-control-next, .carousel-control-prev {
  width: 5%
}
.carousel-control-next-icon, .carousel-control-prev-icon {
  width: 30px;
  height: 30px;
  background-color: #434343;
}
.carousel-indicators {
  position: absolute;
  bottom: -50px
}
.carousel-indicators li {
  background-color : #434343;
}

@media (max-width: 650px) {
  .carousel {
    margin-top: 50px;
  }
}
.logo {
    font-family: 'Nunito', sans-serif;
    margin-left: 15px;
    justify-content: flex-start;
    font-size: 34px;
    color: #36d68b;
    font-weight: 600;
    border: none;
    outline: none;
    background-color: #F5F5F5;
}

.logo:focus, .logo:active {
    outline: none;
    border: none;
}

.nav-container {
    width: 100%;
    display:flex;
    position: fixed;
    top: 0px;
    min-height: 7vh;
    align-items: center;
    justify-content: space-between;
    background-color: whitesmoke;
    border-bottom: 1px solid #36d68b;
    z-index: 3;
}

.nav-links-container {
    
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: space-around;
    width: 50%;
}
.nav-links {
    cursor: pointer;
    margin-right: 20px;
}

.modal-close-btn {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content:center;
    font-weight: 600;
    padding: 5px;
    font-size: 18px;
    border: 1px solid #ffffff;
    border-radius: 50%;
    background-color: #434343;
    color: #ffffff;
    width: 30px;
    height: 30px;
}

.login-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.login-form input {
    padding: 10px !important;
    margin: 10px !important;
    width: 100%;
    margin: 10px;
    border-radius: 10px 
}
.login-form input:focus {
    box-shadow: none;
    border: none
}

.login-btn, 
 .login-btn:hover,
   .login-btn:active {
    font-weight: 500;
    color: #ffffff !important;
    margin: 10px;
    background-color: #36d68b !important;
    border: 1px solid #ffffff !important;
}
.login-btn:focus{
    box-shadow: 0 0 0 0 !important;
}

.go-to-signup, .go-to-signup:hover{
    cursor: pointer;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content:center;
    font-weight: 500;
    padding: 5px 0px;
    border-radius: .25rem;
    background-color: #36d68b;
    color: #ffffff;
}

@media (max-width: 650px) {
    .logo {
        display: none;
    }
    .nav-links-container {
        width: 100%;
    }
    .nav-container {
        border-top: 1px solid #36d68b;
        top: auto;
        top: initial;
        bottom: 0px !important;
    }
}
.upload-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 55px !important;
}

.upload-form {
  position: relative;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 9px;
    max-width: 600px;
    border: 1px solid #9d9d9db1;
    background-color: #36d68b48;
    margin-bottom: 65px !important;
    width: 70%;
  }
  .instructions {
    padding: 10px;
    display: flex;
    flex-direction: column;
    border-radius: 9px;
    max-width: 600px;
    border: 1px solid #9d9d9db1;
    background-color: #36d68b48;
    margin-bottom: 25px !important;
    width: 70%;
    }

  .instructions h5 {
    font-size: 17px;
  }
  .upload-form h3 {
    margin: 9px;
    color: white;
  }
  .upload-input {
    margin: 10px;
    border: 1px solid #36d68b48 !important;
  }
  
  .upload-input:focus {
    box-shadow: none !important;
  }
  .email-input:focus {
    border: 1px solid #36d68b48 !important;
    box-shadow: none !important;

  }
  .upload-form > button {
    font-size: 20px;
    color: #ffffff;
    border-radius: 100px !important;
    margin-top: 30px !important;
    margin-bottom: 20px !important;
    background-color: whitesmoke !important;
    border-color: #747474 !important;
    color: #747474 !important;
  }
  
  .upload-form > button:hover,
    upload-form > button:focus,
      upload-form > button:active {
    font-size: 20px;
    color: #000000;
  }

  .image-upload {
    cursor: pointer;
    margin-top: 20px;
    width: 90px;
    height: 90px;
    border: 1px solid #ffffff;
    background-color: #ffffff;
    border-radius:30px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  #hidden {
      display: none;
  }
  .dropdown {
    margin-top: 15px;
    width: 50%;
  }
  .dropdown-toggle, .dropdown-toggle:focus {
    width: 100%;
    box-shadow: none !important;
      border-color: #36d68b48 !important;
      color: #747474 !important;
    background-color: whitesmoke !important;
    }

  .image-preview {
    border: 2px solid white;
    padding: 10px;
    border-radius: 12px;
    margin-top: 15px;
  }

  .image-preview img {
    width: 100%
  }

  .send-email {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
  }

.prediction-container {
    position: relative;
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin-top: 55px;
}

.image-result {
    background-color: transparent;
    padding: 15px;
    width: 47%;
    display: flex;
    align-items: center;
}
.image-result > img {
    width: 224px !important;
    height: 224px !important;
}
.text-result { 
    background-color: transparent;
    padding: 15px;
    width: 47%
}

.maps-result {
    display: flex;
    justify-content: center;
    margin-bottom: 60px;
}
.maps-result > button, .maps-result > button:hover {
    margin-top: 20px;
}

@media (max-width: 650px) {
    .prediction-container {
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
    }
    .image-result {
        width: 96%;
    }
    
    .text-result { 
        width: 96%;
    }
}
.map-wrapper{
    width: 500px;
    height: 500px;
}
.modal-body{
    width: 468px !important;
    margin-bottom: 30px;
}

@media (max-width: 500px) {
    .modal-body{
        width: 320px !important;
        margin-bottom: 30px;
    }
    .map-wrapper{
        width: 375px;
        height: 375px;
    }
    .modal-dialog{
        margin-top: 40px;
    }
  }
* {
    box-sizing: border-box;
}

.images-grid {
    display: grid;
    margin-top: 12%;
    height: 300px;
    width: 80%;
    margin-left: 10%;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1em;
}

.skin-image {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

@media (max-width: 1150px) {
    .images-grid {
        width: 90%;
        margin-left: 5%;
        margin-top: 15%;
    }
}

@media (max-width: 1010px) {
    .images-grid {
        width: 100%;
        margin-left: 0%;
        margin-top: 20%;
    }
}

@media (max-width: 900px) {
    .images-grid {
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 0;
    }
    .images-grid > .img-2 {
        display: none;
    }
}

@media (max-width: 655px) {
    .images-grid {
        margin-top: 30%;
    }
}

@media (max-width: 400px) {
    .images-grid {
        margin-top: 35%;
    }
}

